<template >
    <el-header>
        <topbar :activeIndex="1"></topbar>
    </el-header>
    <el-main>
        <el-row justify="center">
            <el-col :span="6">
                <el-card class="content">
                    <rules :who="rules"></rules>
                </el-card>
            </el-col>
            <el-col :span="1"></el-col>
            <el-col :span="10">
                <el-card class="content">
                    <freeboard v-if="rules == 1" :re="reload" ></freeboard>
                    <japanboard v-if="rules == 2" :re="reload"></japanboard>
                    <taraboard v-if="rules == 3" :re="reload" :ch="changeside"  @chs="chs"></taraboard>
                    <taraboard10 v-if="rules == 4" :re="reload" :ch="changeside" :es="escape" @chs="chs"></taraboard10>
                    <sooboard v-if="rules == 5" :re="reload" :ch="changeside"  @chs="chs"></sooboard>
                </el-card>
            </el-col>
            <el-col :span="1"></el-col>
            <el-col :span="6">
                <el-card class="content">
                    <menuu @update="re" @ch="ch" :mode="rules" @es="es"></menuu>
                </el-card>
                <br>
                <el-card class="content">
                    <player :player1="cs?`black`:`white`" :player2="cs?`white`:`black`"></player>
                </el-card>
            </el-col>
        </el-row>
    </el-main>
    <el-footer>
    </el-footer>
</template>

<script>
import freeboard from "../components/GomokuBoardFree.vue"
import japanboard from "../components/GomokuBoardJapan.vue"
import taraboard from "../components/GomokuBoardTara.vue"
import taraboard10 from "../components/GomokuBoardTara10.vue"
import sooboard from "../components/GomokuBoardsoo8.vue"
import topbar from "../components/topbar.vue"
import menuu from "../components/menu.vue"
import rules from "../components/rules.vue"
import player from "../components/player.vue"
export default {
    name : "room",
    components:{
        freeboard,
        japanboard,
        taraboard,
        taraboard10,
        sooboard,
        topbar,
        menuu,
        rules,
        player
    },
    props:{
        rules:{

        },
    },
    methods:{
        chs(){
            this.cs = !this.cs;
        },
        re(){
            console.log(this.reload);
            this.reload = !this.reload;
        },
        ch(){
            this.changeside = !this.changeside;
        },
        es(){
            this.escape=!this.escape;
        },
    },
    data(){
        return{
            reload : 1,
            changeside : 1,
            escape:1,
            cs:1,
        }
    },
    beforeMount(){
        this.re();
    },
}
</script>

<style>
    .content {
    vertical-align: middle;
    display:table;
}
</style>